export default {
  data() {
    return {
      show: false,
      workList: "", //作业列表
      course_id: "",
      work_id: "", //作业id
      grade: "" //成绩
    };
  },
  onLoad(options) {
    let t = this;
    t.course_id = options.course_id;
    t.getNoGradeWorkList();
  },
  methods: {
    toShow(work_id) {
      let t = this;
      t.show = true;
      t.work_id = work_id;
    },
    getNoGradeWorkList() {
      let t = this;
      let data = {
        course_id: t.course_id
      };
      t.$utils.ajax(t.$api.gradeWorkList, "get", data, res => {
        console.log(res, "");
        t.workList = res;
      });
    },
    confirm(course_id, work_id) {
      let t = this;
      let data = {
        grade: t.grade,
        course_id: course_id,
        work_id: work_id
      };
      t.$utils.ajax(t.$api.toUpdateGrade, "post", data, res => {
        t.$utils.showToast(res.msg);
        if (res.flag == "yes") {
          t.show = false;
          t.getNoGradeWorkList();
        }
      });
    }
  }
};
